<template>
    <div class="header-box">
      <div class="header-tittle">
        <span v-if="showtittle">{{tittle}}</span>
      </div>
    </div>
</template>

<script>

export default {

  data() {
    return {}
  },
  props: {

    showtittle: {
      type: Boolean,
      default: true
    },
    tittle: {
      type: String,
      default: '首页'
    },
  },
  methods: {
    
  },  

}
</script>

<style lang="less" scoped>
@import "~styles/index.less";
@import "~styles/variable.less";
.header-box{
  width: 100%;
  .h(100);
  .lh(100);
  background-color: black;
  color: @base-header-color;
  .fs(@base-header-size);
  display: flex;
  z-index: 1;
  .header-tittle{
    width: inherit;
    flex: 3;
  }
}
</style>



